import React from "react";
import {WalletTable} from "./UI/WalletTable";


function WalletMain () {
    return(
        <div className="base">
            <div className="base__wrapper base__wrapper--promo">
                <div className="table-base__checker checker">
                    <p className="checker__text"> <span className="check-counter">10</span> </p>
                    <a className="checker__all">  <span></span></a>
                    <a className="checker__cancel"> <span></span></a>
                </div>
                <form action="{{ route(Route::currentRouteName())  }}" style={{display:"none"}} className="sorted-form">
                    <input type="text" name="sort"/>
                </form>
                <WalletTable/>
                <div className="base__bottom">
                    {/*Start paginations */}
                    <div className="paginations">
                        {/*{{$users->appends($arraySort)->links()}}*/}
                        <p className="paginations__info">
                             22  33  300 
                            {/*{{($users->currentPage() * $paginate) - $paginate + 1}} */}
                            {/**/}
                            {/*{{$users->total() < $paginate ? $paginate :  $users->total()}} */}
                            {/**/}
                            {/*{{$users->total()}} */}
                            {/**/}
                        </p>
                    </div>
                    {/*End paginations*/}
                    <button className="base__save" onClick="MainFunction.saveUser();"></button>
                </div>
            </div>
        </div>
    )
};

export {
    WalletMain
}